.bottomSection
  display: flex
  flex-direction: row
  align-items: center
  gap: 30px
  padding-bottom: 10%
  padding-top: 5%
  justify-content: center
  &__logo
    font-family: Arial, sans-serif
    font-size: 20px
    font-weight: 700
    line-height: 23px
    letter-spacing: 0
    text-align: left
    color: #404040
    margin-bottom: 50%
  &__text
    font-family: Arial, sans-serif
    font-size: 36px
    font-weight: 400
    line-height: 41px
    letter-spacing: 0
    text-align: left
    color: #404040

  &__column__about
    width: 308px
    height: 308px
    border: 1px solid #404040
    border-radius: 27px
  &__column__buy
    width: 308px
    height: 308px
    border: 1px solid #3F6FEC
    border-radius: 27px
  &__column__textBlock
    width: 308px
    height: 308px
    font-family: Arial, sans-serif
    font-size: 16px
    font-weight: 400
    line-height: 18px
    letter-spacing: 0em
    text-align: left
    color: #404040
  &__column__aboutText, &__column__buyText
    font-family: Inter, sans-serif
    font-size: 18px
    font-weight: 900
    line-height: 22px
    letter-spacing: 0
    text-align: left
    color: #404040
  &__column__aboutText
    display: block
    margin-top: 84%
    margin-left: 71%
  &__column__buyText
    display: block
    margin-top: 84%
    margin-left: 67%

