/// таблица
.productPage__table__wrap
  display: flex
  flex-direction: column
  margin-top: 6.3%
  margin-left: 5%
  margin-right: 5%
  border: 1px solid rgba(0, 0, 0, 0.2)
  border-radius: 27px

.productPage__table__header
  display: flex
  flex-direction: row

.productPage__table__headerTextWrap:nth-child(5)
  align-items: flex-start

.productPage__table__headerTextWrap,
.productPage__table__headerTextWrapLeft,
.productPage__table__headerTextWrapRight
  display: flex
  align-items: center
  justify-content: center
  border-right: 1px solid rgba(0, 0, 0, 0.2)
  border-bottom: 1px solid rgba(0, 0, 0, 0.2)
  padding: 1.6% 1.55%

.productPage__table__headerTextWrapLeft
  padding-left: 1.5% !important
  padding-right: 26.7% !important

.productPage__table__headerTextWrapRight
  border-right: none !important
  text-align: center

.productPage__table__headerText
  font-family: Inter, sans-serif
  font-size: 24px
  font-weight: 700
  line-height: 29px
  letter-spacing: 0
  text-align: left
  color: rgba(0, 0, 0, 0.5)

// строки в таблице

.productPage__table__row
  display: flex
  flex-direction: row

.productPage__table__rowTextWrap:nth-child(6)
  border-right: none

.productPage__table__rowTextWrap, .productPage__table__rowTextNameWrap
  display: flex
  align-items: center
  justify-content: center
  border-right: 1px solid rgba(0, 0, 0, 0.2)
  border-bottom: 1px solid rgba(0, 0, 0, 0.2)
  padding: 35px 65px 93px 20px

.productPage__table__rowText
  font-family: Inter, sans-serif
  font-size: 24px
  font-weight: 500
