.Product
  display: flex
  flex-direction: column
  background: url(../../../assets/img/ms7.png)
  height: 328px
  width: 645px
  border-radius: 27px
  overflow: hidden
  &__header
    font-family: Inter, sans-serif
    z-index: 5
    padding-left: 4%
    padding-top: 35%
    font-size: 36px
    font-weight: 700
    line-height: 44px
    letter-spacing: 0em
    text-align: left
    color: #FFFFFF
  &__text
    z-index: 5
    font-family: Inter, sans-serif
    padding-left: 4%
    font-size: 14px
    font-weight: 500
    line-height: 17px
    letter-spacing: 0em
    text-align: left
    color: #FFFFFF
  &__img
    margin-top: 26%
    z-index: 3
    height: 164px
    width: 645px
    position: absolute
    background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 90%)
    opacity: 0.7

  .Product__imgWrap
    position: relative


