.LetterSection
  display: flex
  margin-left: 5%
  margin-right: 60px
  flex-direction: column
  margin-top: 10%
  &__header
    font-family: Arial, sans-serif
    font-size: 18px
    font-weight: 400
    line-height: 21px
    letter-spacing: 0
    text-align: left
    color: #404040
    margin-bottom: 4.6%
  &__letters__wrap
    display: flex
    justify-content: space-between
  &__letters__textWrap
    display: flex
    align-items: center
    justify-content: center
    margin-top: 30px
    margin-bottom: 4.3%
  &__letters__text
    font-family: Arial, sans-serif
    height: 144px
    width: 646px
    font-size: 16px
    font-weight: 400
    line-height: 18px
    letter-spacing: 0
    text-align: left
    color: #404040