@import "../../styles-global/mixins"

.CarouselSection
  display: flex
  flex-direction: column
  align-items: center
  margin-top: 7%
  margin-left: 4%

  &__text
    display: flex
    flex-direction: row
    align-items: flex-start
    margin-left: 20%
    gap: 100px
    color: #404040

    &__header
      font-size: 24px
      font-weight: 700
      font-family: Inter, sans-serif
      line-height: 29px

    &__plaintext
      font-size: 16px
      font-weight: 700
      font-family: Inter, sans-serif
      line-height: 19px

.CarouselSection__product
  right: 0
  display: flex
  flex-direction: row
  margin-top: 7%
  gap: 30px
  &__dignities
    display: flex
    flex-direction: column
    gap: 16px
  &__dignitiesConfidence_1
    display: flex
    flex-direction: column
    height: 156px
    width: 156px
    border-radius: 27px
    background-color: #EAC5C5
    &Img
      margin-top: 13.5%
      margin-left: 14.2%
      width: 48%
    &Text
      margin-top: 9%
      margin-left: 14.2%
      font-family: Inter, sans-serif
      font-size: 14px
      font-weight: 600
      line-height: 13px
      letter-spacing: 0
      color: white
      text-align: left
  &__dignitiesConfidence_2
    display: flex
    flex-direction: column
    height: 156px
    width: 307px
    border-radius: 27px
    background-color: #E0EAC5
    &Img
      margin-top: 6.85%
      margin-left: 7.18%
      width: 24.11%
    &Text
      margin-top: 4.6%
      margin-left: 7.18%
      font-family: Inter, sans-serif
      font-size: 14px
      font-weight: 600
      line-height: 13px
      letter-spacing: 0
      color: white
      text-align: left
