.sliderWrap
  width: 1800px

.slider
  border-radius: 27px !important
  height: 488px
  width: 96.6%
  position: relative

.slides
  display: flex
  width: 100%
  height: 100%
  position: relative

.slide
  flex: 1
  height: 100%
  position: relative
  transition: opacity 1s ease, z-index 0.5s ease
  opacity: 0
  display: none
  z-index: 0
  &.active
    display: flex
    opacity: 1
    z-index: 1

.background-image
  width: 100%
  height: 100%
  position: absolute
  top: 0
  left: 0
  z-index: -1
  background-size: cover
  background-position: center center
  background-repeat: no-repeat

.content
  position: relative
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  z-index: 2
  color: white
  text-align: center
  h2
    font-size: 2rem
    margin-bottom: 1rem
  p
    font-size: 1.2rem

.slider-controls
  position: absolute
  top: 1rem
  left: 50%
  transform: translateX(-50%)
  z-index: 2
  .control
    width: 40px
    height: 2px
    border-radius: 10px
    display: inline-block
    margin-right: 1rem
    cursor: pointer
    background-color: white
    opacity: 0.5
    &.active,
    &:hover
      opacity: 1

.text
  font-family: Inter, sans-serif
  font-size: 20px
  font-weight: 700
  line-height: 24px
  letter-spacing: 0em
  text-align: left
  color: #828282

.text1, .text2, .text3, .text4, .text5, .text
  position: absolute

.text1, .text2, .text3
  font-family: Inter, Onest, sans-serif
  font-size: 78px
  font-weight: 900
  line-height: 60px
  letter-spacing: 0
  text-align: left
  color: #404040

.text
  right: 430px
  top: 30px

.text1
  right: -190px
  top: 150px

.text2
  color: #4D5CE1
  right: 25px
  top: 215px

.text3
  right: 10px
  top: 275px

.text4
  font-family: Inter, sans-serif
  font-size: 16px
  font-weight: 700
  line-height: 23px
  letter-spacing: 0
  text-align: left
  width: 560px
  right: -100px
  bottom: 30px


.text5
  font-family: Inter, sans-serif
  font-size: 20px
  font-weight: 700
  line-height: 24px
  letter-spacing: 0
  text-align: left
  right: 8px
  width: 200px
  bottom: 30px


//////
.slide-enter
  opacity: 0
  transform: translateX(-100%)

.slide-enter-active
  opacity: 1
  transform: translateX(0%)
  transition: all 500ms ease-in

.slide-exit
  opacity: 1
  transform: translateX(0%)

.slide-exit-active
  opacity: 0
  transform: translateX(100%)
  transition: all 500ms ease-in

