.OurWorkSection
  margin-top: 7%
  display: flex
  flex-direction: row
  &__row1, &__row2
    display: flex
    flex-direction: column
    &__header
      margin-left: 17%
      text-indent: 52%
      margin-bottom: 100px
      font-family: Inter, sans-serif
      font-size: 78px
      font-weight: 900
      line-height: 60px
      letter-spacing: 0
      text-align: left
      color: #404040
    &__text
      font-family: Arial, sans-serif
      font-size: 14px
      font-weight: 400
      line-height: 16px
      letter-spacing: 0em
      text-align: left
      color: #404040
      display: block
      width: 645px
    &__text2
      font-family: Arial, sans-serif
      font-size: 24px
      font-weight: 400
      line-height: 28px
      letter-spacing: 0em
      text-align: left
    &__block
      margin-top: 4%
      display: flex
      flex-direction: column
      margin-left: 10%
    &__img
      height: 990px
      width: 550px
  &__line__img
    margin-top: 15px
    margin-bottom: 15px
  &__row1
    justify-content: flex-start
  &__row2
    display: flex
    margin-left: 100px
    margin-right: 5%
    &__block
      margin-left: 0 !important

// Видео

.OurWorkSection__video
  margin-top: 8.45%
  &__wrap
    height: 990px
    width: 1320px
    border-radius: 27px
    margin-right: 4.2%
    margin-left: 4.2%
  &__img
    background: url(../../assets/img/Rectangle75.png)
    width: 100%
    height: 100%
    display: flex
    align-items: center
    justify-content: center
  &__button
    background: url(../../assets/img/Ellipse15.png) no-repeat
    width: 126px
    height: 126px
    display: flex
    align-items: center
    justify-content: center
  &__button:hover
    opacity: 0.9

.OurWorkSection__video
  &__textBlock
    margin-top: 4.2%
    display: flex
    flex-direction: row
    margin-left: 4.2%
    margin-right: 4.2%
    gap: 30px
  &__text2
    display: flex
    flex-direction: column
  &__text1
  &__text
    font-family: Arial, sans-serif
    font-size: 16px
    font-weight: 400
    line-height: 18px
    letter-spacing: 0em
    text-align: left
    color: #404040

.OurWorkSection__video__textIndent
  font-family: Arial, sans-serif
  font-size: 16px
  font-weight: 400
  line-height: 18px
  letter-spacing: 0em
  text-align: left
  color: #404040
  text-indent: 20px





