@import "../../styles-global/mixins"

.sectionSlider
  display: flex
  flex-direction: row
  align-items: center
  padding: 3% 4%
  padding-bottom: 0 !important

  &__buttons
    display: flex
    flex-direction: column
    gap: 1rem
    &__textAbout
      right: -22%
      bottom: -34%
      position: relative
      font-size: 24px
      font-weight: 900
      color: $colorWhite
      font-family: Inter, sans-serif
    &__textBuy
      right: -18%
      bottom: -34%
      position: relative
      font-size: 24px
      font-weight: 900
      color: $colorWhite
      font-family: Inter, sans-serif


  &__button__about
    width: 237px
    height: 237px
    border-radius: 27px
    background-color: #404040
  &__button__buy
    width: 237px
    height: 237px
    border-radius: 27px
    background-color: #3F6FEC

@media screen and (max-width: 1440px)
    .sectionSlider__buttons__textAbout, .sectionSlider__buttons__textBuy
      font-size: 18px
