.CatalogSection
  display: flex
  flex-direction: column
  margin-top: 9%

  &__header
    font-family: Inter, sans-serif
    text-indent: 21.5%
    font-size: 78px
    font-weight: 900
    line-height: 60px
    text-align: left
    color: #404040
    margin-left: 8%
  &__colorText
    color: #4D5CE1
  &__products
    display: flex
    gap: 66px

    flex-direction: column
  &__products__row1Wrap
    display: flex
    flex-direction: column
    gap: 18px

  &__products__row1, &__products__row2
    justify-content: center
    gap: 28px
    display: flex
    flex-direction: row
  &__products__row1
    align-items: flex-end
  &__products__row1__elemWrap
    display: flex
    flex-direction: column
    justify-content: flex-start

  &__products__row1__elem
    height: 360px
    width: 308px
    border-radius: 27px
    background: #f9f6fd

  &__products__row1__elemBig
    height: 360px
    width: 646px
    border-radius: 27px
    background: #f9f6fd

  &__products__row1__text
    font-family: Inter, sans-serif
    font-size: 64px
    font-weight: 900
    line-height: 49px
    letter-spacing: 0em
    text-align: left
    color: #4D5CE1
  &__products__row1__elemText
    font-family: Inter, sans-serif
    font-size: 24px
    font-weight: 700
    line-height: 29px
    letter-spacing: 0
    text-align: left
    color: #404040


