@import "../ProductPage.module"

.productPage__header__wrap
  display: flex
  flex-direction: row
  margin-top: 4.9%
  padding-left: 4.2%
  margin-bottom: 2.1% !important

.productPage__header__back
  padding: 13px 14px
  margin-left: 60px

.productPage__header__route
  padding: 13px 14px
  color: #404040

.product__header__route
  position: relative
  margin-left: 1.36%
  z-index: 10000 !important
  font-family: Inter, sans-serif
  font-size: 14px
  font-weight: 500
  line-height: 17px
  padding: 13px 14px
  background-color: #e1e1e1
  border-radius: 40px
  letter-spacing: 0
  text-align: left
  color: #404040

.productPage__content__wrap
  display: flex
  flex-direction: row
  gap: 38px
  justify-content: center

.productPage__content__elem1
  height: 520px
  width: 472px
  border-radius: 27px
  background: url(../../../assets/img/MS3.png)

.productPage__content__elem2
  height: 520px
  width: 472px
  border-radius: 27px
  background: url(../../../assets/img/ms33.png)

.productPage__content__elem1Text, .productPage__content__elem2Text
  font-family: Arial, sans-serif
  padding-top: 7.42%
  padding-left: 5.6%
  display: block
  font-size: 24px
  font-weight: 700
  color: white
  line-height: 18px
  letter-spacing: 0em
  text-align: left

.productPage__content__elem1TextWrap, .productPage__content__elem2TextWrap
  margin-top: 94%
  border-bottom-left-radius: 27px
  border-bottom-right-radius: 27px
  width: 100%
  height: 15.3%
  background: #EBEBEB99
  backdrop-filter: blur(10px)

.productPage__content__text1
  font-family: Inter, sans-serif
  font-size: 54px
  font-weight: 600
  line-height: 42px
  letter-spacing: 0
  text-align: left
  color: #404040

.productPage__content__text2
  font-family: Inter, sans-serif
  font-size: 16px
  font-weight: 700
  line-height: 19px
  letter-spacing: 0
  text-align: left
  margin-top: 17.5%
  margin-bottom: 5%
  display: block
  color: #404040


.productPage__content__text3
  height: 340px
  display: block
  width: 258px
  font-family: Inter, sans-serif
  font-size: 14px
  font-weight: 500
  line-height: 17px
  letter-spacing: 0
  color: #404040
  text-align: left

.productPage__product__wrap
  display: flex
  flex-direction: column
  margin-top: 9.7%

.productPage__product__row
  display: flex
  flex-direction: row
  gap: 7px
  justify-content: center

.productPage__product__elem
  position: relative
  height: 348px
  width: 435px
  border-radius: 27px
  border: 1px solid #d2d2d2

.productPage__product__header, .productPage__product__type
  font-family: Inter, sans-serif
  font-size: 32px
  font-weight: 700
  line-height: 39px
  letter-spacing: 0
  text-align: left
  display: block
  margin-left: 5%
  z-index: 2

.productPage__product__header
  color: #404040
  margin-top: 5%

.productPage__product__img
  z-index: -1
  position: absolute
  top: 8%
  right: 0

.productPage__product__text1, .productPage__product__text2
  font-family: Inter, sans-serif
  font-size: 24px
  font-weight: 700
  line-height: 29px
  letter-spacing: 0
  text-align: left
  color: #404040

.productPage__product__text2
  margin-left: 25%

.productPage__product__textWrap
  margin-left: 5%
  display: flex
  flex-direction: row
  margin-top: 38%

.productPage__header__2
  margin-left: 5%

.CatalogSection__products__row1
  margin-bottom: 10%

.navigationProduct
  z-index: 4
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center


.navCircleProduct1
  z-index: 3
  position: absolute
  margin-bottom: 3%
  margin-left: 1.3%
  width: 35px
  height: 30px
  border-radius: 50%
  background-color: #ffffff

.navCircleProduct2
  margin-top: 3%
  margin-left: 1.3%
  z-index: 3
  position: absolute
  width: 35px
  height: 30px
  border-radius: 50%
  background-color: #ffffff

.navConnectorProduct
  position: absolute
  margin-left: 20px
  width: 40px
  height: 25px
  background: #e1e1e1

.navLinkBackProduct
  display: flex
  align-items: center
  justify-content: center

